import React from 'react';
import FlowComponent from '../../common/component/core/FlowComponent';
import {repeatFloatAdvance} from '../../../actions/loan_actions';
import {Alert, Row, Col, Badge } from 'react-bootstrap';
import '../../../styles/flow.css';
import {Link} from 'react-router-dom';
import {dd_value} from '../../../actions/common_actions';
import DisplayImage from '../../common/component/DisplayImage';
import  {variant_color, format_date} from '../../../helpers/common_helper';
import {person_name, calc_percent} from '../../../helpers/common_helper';
import { Modal , Button} from 'react-bootstrap';
import AccountContainer from '../../account/container/account_container';
import CustomerStatus from '../../common/component/core/CustomerStatus';
import  {lbl_status, style_status} from '../../../helpers/common_helper';
import {bring_to_probation} from '../../../actions/borrower_actions';
import  {check_priv} from '../../../helpers/storage_helper';
import Agreement from './Agreement';
import { event } from 'jquery';
class BorrowerPanel extends FlowComponent {

	handleViewAccountsClick = (event,data_prvdr_cust_id,person_name) => {
    this.setState({cust_id: event.target.id});
    this.data_prvdr_cust_id =  data_prvdr_cust_id;
    this.person_name = person_name;
    this.setState({modalBox: "true"});   
  }
  	handleModalClose = (event) => {
      this.setState({ modalBox: false });
  } 
  	

  	bring_to_probation = (event) => {
	  	const resp = window.confirm("Are you sure to bring the customer to probation?");
		if(resp == false){
			return;
		}
		  
		  const request = {"cust_id" : this.props.customer.cust_id};
		  bring_to_probation(this.req(request))
		       .then((response) => {
		        if(!response){return };
		            if(response.status === "success"){
		              window.location.reload();
		            }
		            else{
		              alert(response.message + "\n" + JSON.stringify(response.data));  
		            }
		          }
		        ); 
	      //this.setState({credit_score_factor_id: event.target.id, toView: true});
	   } 
	   
	repeatFloatAdvance = (event) => {
		const request = {"cust_id" : this.props.customer.cust_id};
		repeatFloatAdvance(this.req(request))
			.then((response) => {
				if(!response){return };
					if(response){ 	
						alert("Float Advance Application created successfully")
						window.location.reload();
					}	
			});
	}
	render(){
		
		const bring_to_prob_priv = check_priv('customer' , 'bring_to_probation');
		console.log(this.props.customer);
		return(
			<Alert variant="">
					<>
						<td><b>Customer</b>&nbsp;&nbsp;</td>
									{/*<Badge pill variant={variant_color(this.props.customer.status)}>{dd_value(this.props.customer.status)}</Badge> &nbsp;*/}
						
						<td style={{padding:'5px'}}><CustomerStatus show_1l="true" id={this.props.customer.id} entity="borrowers" 
							status={this.props.customer.status} category={this.props.customer.category} 
							prob_fas={this.props.customer.prob_fas}/>&nbsp;
						</td>
						
						<td style={{padding:'5px'}}><input id={this.props.customer.cust_id} type='button' 
							onClick={(e) => this.handleViewAccountsClick(e, this.props.customer.data_prvdr_cust_id,person_name(this.props.customer))} 
							value='View A/C'  className={`btn btn-primary ${this.btn_style}`} />&nbsp;
						</td>

						<td style={{padding:'5px'}}><Link to={"/report/growth_chart_for/"+this.props.customer.cust_id} target="_blank" className="btn btn-primary">Growth</Link></td>
						
						{(this.props.customer.prob_fas == 0 || this.props.customer.prob_fas == null)&& bring_to_prob_priv &&
							<td style={{padding:'5px'}}><Button onClick={this.bring_to_probation}>Bring to Probation</Button></td>
						}
						<td style={{padding:'5px'}}><Link to={"/fa/view/"+this.props.customer.last_loan_doc_id} target="_blank">View Last FA</Link></td>
						
						{/* { this.props.customer.ongoing_loan_doc_id == null && this.props.customer.pending_loan_appl_doc_id == null &&  
							<td><Button onClick={this.repeatFloatAdvance}>Repeat FA</Button></td>} */}

						
					</>
					
					<Row className="panel_border">
						<Col lg="12">
							<Row>
								<Col>Data Provider Customer ID</Col>
								<Col><b>{this.props.customer.data_prvdr_cust_id}</b></Col>
								<Col>FLOW Customer ID</Col>
								<Col><b>{this.props.customer.cust_id}</b></Col>
							</Row>
							<Row>
								<Col>Business Name</Col>
								<Col><b>{this.props.customer.biz_name}</b></Col>
								<Col>Data Provider</Col>
								<Col><b>{this.props.customer.data_prvdr_name}</b></Col>
							</Row>
							<Row>
								<Col>Business Address Property Type</Col>
								<Col><b>{dd_value(this.props.customer.biz_addr_prop_type)}</b></Col>
								<Col>Lender</Col>
								<Col><b>{this.props.customer.lender_name}</b></Col>
							</Row>
							<Row>
								<Col>Master Customer ID</Col>
								<Col><b>{this.props.customer.master_cust_id}</b></Col>
								<Col>Data Provider's Relationship Manager</Col>
								<Col><b>{this.props.customer.dp_rel_mgr_name}</b></Col>
							</Row>
							<Row>
								
								<Col>Flow Relationship Manager</Col>
								<Col><b>{this.props.customer.flow_rel_mgr_name}</b></Col>
								<Col>Score Run ID</Col>
								<Col><b>{this.props.customer.csf_run_id}</b></Col>
							</Row>
							<Row>
								<Col>Registered Date</Col>
								<Col><b>{format_date(this.props.customer.reg_date)}</b></Col>
								
								
								<Col>
								{this.props.customer.photo_biz_lic ?
								<Link to={this.props.customer.photo_biz_lic_path + "/l_" + this.props.customer.photo_biz_lic} target="_blank">
								Business Licence Photo
								</Link> 
								: 
								<p>Business Licence Photo : Not uploaded</p>
								}
								</Col>
								<Col></Col>
							</Row>

							{/*<Row>
								<Col><Link to={this.props.customer.photo_biz_lic_path + "/l_" + this.props.customer.photo_biz_lic} target="_blank">
								Business Licence Photo
								</Link></Col>
							</Row>*/}
							<Row>
								<Col lg="4">
									<DisplayImage title="Selfie" image_path={this.props.customer.photo_selfie_path} file_name={this.props.customer.photo_selfie} />
								</Col>
								<Col lg="4">
									
									<DisplayImage title="Owner Photo" image_path={this.props.customer.photo_pps_path} file_name={this.props.customer.photo_pps} />
									{/*<b>Owner Photo</b><br/>
									<img src={this.root + this.props.customer.photo_pps_path + "/s_" + this.props.customer.photo_pps} 
									alt={this.props.customer.photo_pps_path + "/s_" + this.props.customer.photo_pps}/>*/}
								</Col>
								<Col lg="4">
									
									<DisplayImage title="Establishment Photo" image_path={this.props.customer.photo_shop_path} file_name={this.props.customer.photo_shop} />
									{/*<b>Establishment Photo</b><br/>
									<img src={this.root + this.props.customer.photo_shop_path + "/s_" + this.props.customer.photo_shop} 
							alt={this.props.customer.photo_shop_path + "/s_" + this.props.customer.photo_shop}
							/>*/}
								</Col>
							</Row>

						</Col>
						{/*<Col lg="3">
							<b>Owner Photo</b><br/>

							<img src={this.root + this.props.customer.photo_pps_path + "/s_" + this.props.customer.photo_pps} 
							alt={this.props.customer.photo_pps_path + "/s_" + this.props.customer.photo_pps}/>
						</Col>*/}


					</Row>
					<Row style={{padding:'20px 0 0 0'}}>
						      <Col md={2}>
		                       <Alert.Heading><b>Agreement</b></Alert.Heading>
						      </Col>
					</Row>
					<Row className="panel_border">
						<Agreement mode="create" variant={this.state.variant} 
							customer={this.props.customer} cust_agreement_status={this.props.cust_agreement_status} 
						/>	
						<div style={{width:'100%'}}>
							<Link to={this.props.customer.cust_id ? `/agreement/list/${this.props.customer.cust_id}` : "#"} 	 
							className={this.props.customer.account_id ? "agreement" : "agreement_disable"}
							onClick={this.handleAgreementLinkClick} target="_blank">List Customer Agreements</Link>
							{check_priv('cust_agreement','create') &&
								<Link to={this.props.customer.cust_id ? `/agreement/create/${this.props.customer.cust_id}` : "#"} 	 
								className={this.props.customer.account_id ? "agreement" : "agreement_disable"}
								onClick={this.handleAgreementLinkClick} target="_blank">Generate New Customer Agreement</Link>
							}
							{check_priv('cust_agreement','sign_upload') &&
								<Link to={`/agreement/upload/${this.props.customer.cust_id}`} 
								className={this.props.customer.account_id ? "agreement" : "agreement_disable"} 
								onClick={this.handleAgreementLinkClick} target="_blank">Sign / Upload Agreement</Link>
							}
						</div>		
					</Row>
							
					<Modal show={this.state.modalBox} onHide={this.handleModalClose} size="xl">
						<Modal.Header closeButton show={this.state.modalBox} onHide={this.handleModalClose}>
							<Modal.Title>Customer Accounts : {this.state.cust_id}</Modal.Title>
						</Modal.Header>
						<Modal.Body>
							<AccountContainer entity="customer" dp_cust_id={this.data_prvdr_cust_id} cust_name={this.person_name} entity="customer" entity_id={this.state.cust_id}/>
						</Modal.Body>
						<Modal.Footer>
							<Button variant="secondary" onClick={this.handleModalClose}>Close</Button>
						</Modal.Footer>
					</Modal>
			</Alert>
			);
	}

}

export default BorrowerPanel;