import React from 'react';
import FlowContainer from '../../common/container/core/FlowContainer';
import {list_lender_txn_stmts} from '../../../actions/lender_actions';
import $ from 'jquery';
import {rt_algn,lbl_amt} from '../../../helpers/common_helper';
import {format_date_tbl} from '../../../helpers/common_helper';
import {dd_value} from '../../../actions/common_actions';
import {ImArrowUpRight,ImArrowDownLeft} from "react-icons/im";
import {Button} from 'react-bootstrap';

class ListTodayAccountStmtTxnContainer extends FlowContainer{

  state = {
    last_updated : null,
    updated_since : 0,
    timer :0,
    buttonName : 'Pause Refresh',
    btn_variant : 'danger',
    timerID : '',
  }
 
  constructor(){
    super()
    this.REFRESH_IN = 30
  }
 componentWillUnmount() {
    
    clearInterval(this.timerID);
  }

  tick() {
      if(this.state.timer !=0){
        this.setState({
          timer: this.state.timer - 1,
          updated_since : this.REFRESH_IN - this.state.timer 
        });
      }else{
      
      this.refreshPaymentsList();
      }
    }


    componentWillMount()
    {

     this.refreshPaymentsList();

    }


    chk_acc_type(cr_amt,dr_amt){
        if (cr_amt === 0 && dr_amt === 0) {
          return null
        }
        else if (cr_amt === 0){
          return <ImArrowUpRight color="red"/>
        }
        else if (dr_amt === 0){
          return <ImArrowDownLeft color = "green"/>
        }else{
          return null
        }

    }

   
    pauseRefresh = () => {
        if(this.timerID){
            clearInterval(this.timerID);
            this.timerID = null
            this.setState({buttonName : 'Start Refresh',
                           btn_variant : 'success' })    
        }else{
            this.timerID = setInterval(() => this.tick(),1000);
            this.setState({buttonName : 'Pause Refresh',
                           btn_variant : 'danger'})
        }
        
        

    }


    refreshPaymentsList = () =>{
       
        clearInterval(this.timerID);
        var statement_search = {today : true}
        statement_search.acc_id = this.props.match.params.acc_id;

        list_lender_txn_stmts(this.req({statement_search}))
            .then((response) => {
                var prev_table = $('#statement_search').DataTable();
                prev_table.destroy();
                if(!response)
                {
                    return null;
                }

                if(response.status === "success"){
                    var account_num = response.data.acc_txt
                    const lenderTxnStmts = response.data.account_txns.map((txn,i)=>
                          <tr key={i}>
                          <td>{++i}</td>
                          <td>{this.chk_acc_type(txn.cr_amt,txn.dr_amt)}</td>
                          <td > {dd_value(txn.stmt_txn_type)}</td>
                          <td > {rt_algn(lbl_amt(txn.cr_amt, this.currency_code))}</td>
                          <td > {rt_algn(lbl_amt(txn.dr_amt, this.currency_code))}</td>
                          <td > {rt_algn(lbl_amt(txn.balance, this.currency_code))}</td>
                          <td > {txn.stmt_txn_id}</td>
                          <td > {txn.descr}</td>
                          <td > {format_date_tbl(txn.stmt_txn_date,true)}</td>
                          <td > {txn.account_id}</td>
                          <td > {txn.loan_doc_id}</td>
                          
                            
                          </tr>
                     );
                    this.timerID = setInterval(() => this.tick(),1000);

                    this.setState({last_updated: (new Date().toLocaleTimeString('en-US', { hour: 'numeric', minute: 'numeric',second: 'numeric', hour24: true })).toString(), 
                                    timer : this.REFRESH_IN,
                                    lenderTxnStmts,
                                    account_result:"show_div",
                                    account_num:  account_num });
                    
                        $('#statement_search').removeAttr('width').dataTable({
                            "pageLength": 20,
                            "order" : [],
                                "columns": [
                                  {"width" : "10%"},
                                  {"width" : "10%"},
                                  {"width": "10%"},
                                  {"width": "10%"},
                                  {"width": "15%"},
                                  {"width": "15%"},
                                  {"width": "20%"},
                                  {"width": "20%"},
                                  {"width": "20%"},
                                  {"width": "20%"},
                                  {"width": "20%"},
                                ],
                                dom:'Bfrtip',
                                buttons: [
                                'csv','excel','print'
                                ]
                        });        
                }else{
                  alert(response.message + "\n" + JSON.stringify(response.data));  
                }

  });
  }
get_message = () => {
    if(!this.timerID){
        return <h4 style={{color: "red"}}>Paused Refreshing</h4>
    }
    if(this.state.timer != 0){ 
        return <h4 style={{color: "blue"}}>Refreshing in {this.state.timer} seconds</h4>
    }else{
        return <h4 style={{color: "green"}}>Refreshing please wait.</h4>
    }
               
}

render(){
    return(
        <div id=""><br/>
        <div className="container">
            <div className="form-row">
                <div className = "col-md-6">
                    <h3 className="lender_accounts">Today's Txns of {this.state.account_num}</h3>
                </div>

                <div className = "col-md-3">
                    {this.get_message()}
                </div>
               
                <div className = "col-md-3">
                    <Button variant="primary" onClick={this.refreshPaymentsList}>Refresh Now</Button> &nbsp; 
                    <Button variant={this.state.btn_variant} onClick={this.pauseRefresh}>{this.state.buttonName}</Button> 
                </div>
            </div>

            <div className="form-row mt-3">
                {this.state.last_updated && 
                <h5>Last updated : {this.state.last_updated} ({this.state.updated_since}s ago)</h5>
                }
            </div>

            
            <br/>
           
            <div className={this.state.account_result}>
                <div id='datatable '>
                 
                    <table id ="statement_search">
                    <thead>
                        <tr>
                            <th>S.No</th>
                            <th></th> 
                            <th>Transaction Type</th>
                            <th>Credit</th>
                            <th>Debit</th> 
                            <th>Balance</th>
                            <th>Transaction ID</th>
                            <th>Description</th> 
                            <th>Transaction Date</th> 
                            <th>Account ID</th>
                            <th>Loan Document ID</th>
                        </tr>
                    </thead>
                    <tbody> 
                        {this.state.lenderTxnStmts}
                    </tbody>
                    </table>          
                </div>
            </div>
        </div>
        </div>




      )


}
}


export default ListTodayAccountStmtTxnContainer;